<template>
    <el-dialog class="add-dialog" :title="title" :visible.sync="dialogVisible" @close="resetForm" fullscreen :modal="false">
        <el-tabs v-model="activeName">
            <el-tab-pane label="基础信息" name="first">
                <techo-people ref="vBaseForm" :isAdd="isAdd" :dic="dic" @onBasePeople="handleQueryBasePeople" @onPosition="handlePosition"></techo-people>
            </el-tab-pane>
            <el-tab-pane label="位置信息" name="second">
                <vPeoplePosition ref="vPeoplePosition" :xingzhengqu="dic['行政区划']" :disCodeOption="disCodeOption" :isAdd="isAdd"></vPeoplePosition>
            </el-tab-pane>
            <el-tab-pane label="附加信息" name="third">
                <el-form ref="dialogForm" :model="dialogForm" :rules="teamRules" label-width="120px">
                    <el-row :gutter="15">
                        <el-col :span="6">
                            <el-form-item label="管控人姓名" prop="managementName">
                                <el-input v-model="dialogForm.managementName" :maxlength="18" placeholder="请填写管控人姓名"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="管控人联系方式" prop="managementContact">
                                <el-input v-model="dialogForm.managementContact" :maxlength="12" placeholder="请填写管控人联系方式"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="初次发现时间" prop="discoveredFirstTime">
                                <el-date-picker v-model="dialogForm.discoveredFirstTime" type="date" placeholder="请选择日期"></el-date-picker>
                            </el-form-item> 
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="管控情况">
                                <el-select placeholder="请选择管控情况" clearable v-model="dialogForm.managementSituation">
                                    <el-option v-for="(item, index) in dic['管控情况']" :label="item.name" :value="item.code" :key="index"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="24">
                            <el-form-item label="帮扶情况">
                                <el-input v-model="dialogForm.helpSituation" type="textarea" :rows="3" :maxlength="1024" placeholder="请填写帮扶情况"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="6">
                            <el-form-item label="帮扶人姓名" prop="helpName">
                                <el-input v-model="dialogForm.helpName" placeholder="请填写帮扶人姓名"></el-input>
                            </el-form-item>  
                            <el-form-item label="有无犯罪史" prop="isCriminal">
                                <el-radio v-model="dialogForm.isCriminal" :label="1">有</el-radio>
                                <el-radio v-model="dialogForm.isCriminal" :label="0">无</el-radio>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="帮扶人联系方式" prop="helpContact">
                                <el-input v-model="dialogForm.helpContact" :maxlength="12" placeholder="请填写管控人联系方式"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="吸毒原因">
                                <el-select placeholder="请选择吸毒原因" clearable v-model="dialogForm.reasons">
                                    <el-option v-for="(item, index) in dic['吸毒原因']" :label="item.name" :value="item.code" :key="index"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="吸毒后果">
                                <el-select placeholder="请选择吸毒后果" clearable v-model="dialogForm.drugsResult">
                                    <el-option v-for="(item, index) in dic['吸毒后果']" :label="item.name" :value="item.code" :key="index"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="24">
                            <el-form-item label="犯罪情况">
                                <el-input v-model="dialogForm.criminalSituation" :disabled="dialogForm.isCriminal == 0" type="textarea" :rows="3" :maxlength="1024" placeholder="请填写犯罪情况"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
            </el-tab-pane>
        </el-tabs>
        <div slot="footer" class="dialog-footer">
			<el-button v-if="activeName == 'first'" type="primary" size="small" @click="activeName = 'second'">下一步</el-button>
            <el-button v-if="activeName == 'second'" type="primary" size="small" @click="activeName = 'first'">上一步</el-button>
            <el-button v-if="activeName == 'second'" type="primary" size="small" @click="activeName = 'third'">下一步</el-button>
			<el-button v-if="activeName == 'third'" type="warning" size="small" @click="resetForm">取消</el-button>
            <el-button v-if="activeName == 'third'" type="primary" size="small" @click="activeName = 'second'">上一步</el-button>
            <el-button v-if="activeName == 'third'" type="success" size="small" @click="submitForm">保存</el-button>
		</div>
    </el-dialog>
</template>
<script>
import service from "./services/special_people_service.js";
import vPeoplePosition from "../infact_peoples/components/PeoplePosition.vue";

export default {
  props: {
    disCodeOption: {
      type: Array,
      default: []
    }
  },
  components: {
    vPeoplePosition
  },
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      activeName: "first",
      dic: {
        性别: [],
        民族: [],
        婚姻状况: [],
        政治面貌: [],
        学历: [],
        宗教信仰: [],
        职业类别: [],
        行政区划: [],
        管控情况: [],
        吸毒原因: [],
        吸毒后果: []
      },
      dialogForm: {
        isCriminal: 0,
        drugsResult: "100", // 吸毒后果
        managementSituation: "01" // 管控情况
      },
      teamRules: {
        discoveredFirstTime: [
          { required: true, message: "请选择初次发现时间", trigger: "change" }
        ],
        managementName: [
          { required: true, message: "请输入管控人姓名", trigger: "blur" },
          { min: 2, max: 12, message: "长度在 2 到 10 个字符", trigger: "blur" }
        ],
        managementContact: [
          { required: true, message: "请输入管控人联系方式", trigger: "blur" },
          util.validate.tellAndPhone()
        ],
        helpName: [
          { required: true, message: "请输入帮扶人姓名", trigger: "blur" },
          { min: 2, max: 12, message: "长度在 2 到 10 个字符", trigger: "blur" }
        ],
        helpContact: [
          { required: true, message: "请输入帮扶人联系方式", trigger: "blur" },
          util.validate.tellAndPhone()
        ]
      },
      houseInfo: {}
    };
  },

  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            if (item.name == "职业类别") {
              that.dic[item.name] = that.handleTreeData(data);
            } else {
              that.dic[item.name] = data;
            }
          });
        }
      });
    });
  },

  watch: {
    activeName(name) {
      if (name == "second") {
        this.$refs["vBaseForm"].baseForm.id &&
          service.requestBaseIdByPeople(
            "people/drugs/findPage",
            this.$refs["vBaseForm"].baseForm.id,
            data => {
              if (data.totalCount != 0) {
                this.isAdd = false;
                this.dialogForm = data.list[0];
              }
            }
          );
        this.$refs["vBaseForm"].$refs["baseForm"].validate(val => {
          if (!val) {
            this.$nextTick(() => {
              this.activeName = "first";
            });
          }
        });
        this.$refs["vPeoplePosition"].$refs["houseItem"] &&
          this.$refs["vPeoplePosition"].$refs["houseItem"].initGridData();
        this.$refs["vPeoplePosition"].$refs["liveHouseItem"] &&
          this.$refs["vPeoplePosition"].$refs["liveHouseItem"].initGridData();
      } else if (name == "third") {
        new Promise((resolve, reject) => {
          if (this.$refs["vPeoplePosition"].showHujiForm == true) {
            this.$refs["vPeoplePosition"].$refs["houseItem"].$refs[
              "houseItem"
            ].validate(val => {
              if (!val) {
                this.$nextTick(() => {
                  this.activeName = "second";
                });
              }
            });
            this.houseInfo = this.$refs["vPeoplePosition"].$refs[
              "houseItem"
            ].houseInfo;
            resolve();  
          } else {
            service.requestHouseInfo(
              this.$refs["vBaseForm"].baseForm.houseId,
              data => {
                this.houseInfo = data;
                resolve();  
              }
            );
          };
          if (this.$refs["vPeoplePosition"].showXianzhuForm == true) {
            this.$refs["vPeoplePosition"].$refs["liveHouseItem"].$refs[
              "liveHouseItem"
            ].validate(val => {
              if (!val) {
                this.$nextTick(() => {
                  this.activeName = "second";
                });
              }
            });
          };
        }).then(() => {
          this.dialogForm = {
            ...this.dialogForm,
            districtCode: this.houseInfo.districtCode
          };
        });
      }
    },
    "dialogForm.isCriminal"(value) {
      value == 0 && (this.dialogForm.criminalSituation = "");
    }
  },

  methods: {
    // 职业类型组成树形数据
    handleTreeData(val) {
      // 大类
      let itemONE = _.map(
        _.filter(val, street => {
          return street.code.slice(1, 5) === "0000";
        }),
        street => {
          return {
            code: street.code,
            label: street.name,
            children: []
          };
        }
      );
      // 中类
      _.map(itemONE, item => {
        item.children = _.map(
          _.filter(val, street => {
            return (
              street.code.slice(0, 1) === item.code.slice(0, 1) &&
              street.code.slice(1, 5) !== "0000" &&
              street.code.slice(3, 5) === "00"
            );
          }),
          street => {
            return {
              code: street.code,
              label: street.name,
              children: []
            };
          }
        );
      });
      // 小类
      _.map(itemONE, midItem => {
        _.map(midItem.children, item => {
          item.children = _.map(
            _.filter(val, street => {
              return (
                street.code.slice(0, 3) === item.code.slice(0, 3) &&
                street.code.slice(3, 5) !== "00"
              );
            }),
            street => {
              return {
                code: street.code,
                label: street.name
              };
            }
          );
        });
      });
      return itemONE;
    },
    /** 身份证号选择后回填位置信息 */
    handlePosition(item) {
        this.$refs["vPeoplePosition"].hujidi = item.domicilePlaceDetail;
        this.$refs["vPeoplePosition"].xianzhudi = item.livePlaceDetail;
        this.$refs["vPeoplePosition"].showHujiForm = false;
        this.$refs["vPeoplePosition"].showXianzhuForm = false;
    },
    handleQueryBasePeople(idCardNo, callback) {
      service.requestBasePeopleList(idCardNo, data => {
        callback && callback(data.list);
      });
    },
    /** 保存 */
    submitForm() {
      this.$refs["dialogForm"].validate(valid => {
        if (valid) {
          let baseInfo = _.cloneDeep(this.$refs["vBaseForm"].baseForm);
          baseInfo.birthDate = baseInfo.birthDate && util.time.formatDate(
            baseInfo.birthDate,
            "YYYY-MM-DD"
          );
          baseInfo.occupationType = _.last(baseInfo.occupationType);
          baseInfo.districtCode = this.houseInfo.districtCode;
          if (this.$refs["vPeoplePosition"].showHujiForm == true) {
            let houseAddress = this.$refs["vPeoplePosition"].$refs["houseItem"]
              .positionForm;
            baseInfo.domicilePlace = houseAddress.province; // 户籍地
            baseInfo.domicilePlaceDetail = houseAddress.domicilePlaceDetail; // 户籍地详址
            baseInfo.houseId = houseAddress.houseId; // 户籍地户ID
          }
          if (this.$refs["vPeoplePosition"].showXianzhuForm == true) {
            let liveAddress = this.$refs["vPeoplePosition"].$refs[
              "liveHouseItem"
            ].positionForm;
            baseInfo.livePlace = liveAddress.province; // 居住地
            baseInfo.livePlaceDetail = liveAddress.domicilePlaceDetail; // 居住地详址
            baseInfo.liveHouseId = liveAddress.houseId || ""; // 居住地户ID
          }
          baseInfo.dicMap && delete baseInfo.dicMap;
          if (baseInfo.id) {
            // 修改基本信息
            service.requestUpdatePeopleInfo(baseInfo, data => {
              this.comminForm(data);
            });
          } else {
            // 添加基本信息
            service.requestSavePeopleInfo(baseInfo, data => {
              this.comminForm(data);
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    comminForm(data) {
      let otherInfo = _.cloneDeep(this.dialogForm);
      otherInfo.discoveredFirstTime = otherInfo.discoveredFirstTime && util.time.formatDate(
        otherInfo.discoveredFirstTime,
        "YYYY-MM-DD"
      );
      otherInfo.dicMap && delete otherInfo.dicMap;
      if (this.isAdd == true) {
        otherInfo.peopleId = data.id;
        // 添加 吸毒人员附加信息
        service.requestSaveDrugsterPeople(otherInfo, data => {
          this.$popup.showNotification({
            type: "success",
            message: "吸毒人员添加成功！"
          });
          this.resetForm();
          this.$parent.handleQuery();
        });
      } else {
        // 修改
        let param = _.pick(otherInfo, [
          "criminalSituation",
          "discoveredFirstTime",
          "districtCode",
          "drugsResult",
          "helpContact",
          "helpName",
          "helpSituation",
          "id",
          "isCriminal",
          "managementContact",
          "managementName",
          "managementSituation",
          "peopleId",
          "reasons"
        ]);
        service.requestUpdateDrugsterPeople(param, data => {
          this.$popup.showNotification({
            type: "success",
            message: "吸毒人员修改成功！"
          });
          this.resetForm();
          this.$parent.handleQuery();
        });
      }
    },
    /** 取消 */
    resetForm() {
      this.$refs["vBaseForm"].$refs["baseForm"].resetFields();
      this.$refs["vBaseForm"].resetBaseForm();
      this.$refs["dialogForm"].resetFields();
      this.$refs["vPeoplePosition"].destroyedForm();
      this.dialogForm = {
        isCriminal: 0
      };
      this.houseInfo = {};
      this.$data.dialogVisible = false;
      this.activeName = "first";
      this.isAdd = true;
    }
  }
};
</script>
<style>
.add-dialog .el-date-editor.el-input {
  width: 100%;
}
.add-dialog .el-select {
  width: 100%;
}
.add-dialog .el-cascader {
  width: 100%;
}
</style>